<template>
    <div id="app">
  <div class="row " style="margin:0" >
    <div class="col s6 p-0 mg-l-1" style="left:0px height : 10px">
      <div class="card-panel teal">
        <span class="card-title white-text">해시 등록</span>
        <input class="white-text" type="text" v-model="message" v-on:keyup.enter="submit" placeholder="4자 이내" required="required" minlength="1" maxlength="4" style="width:150px;">
        <transition name="fade">
          <span class="card-title red-text" v-show="warning">이미 등록된 해시입니다.</span>
        </transition>
      </div>
    </div>
    <div class="col s6">
      <div class="card-panel teal">
        <div class="chip" v-for="(item,key) in array" :key="key">{{item}} <i class="close material-icons" v-on:click="remove(key)">x</i> </div>
      </div>
    </div>
  </div>
</div>
</template>

<style >
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 222
  
}
</style>
<script>
export default {
  name : 'App',
  data() {
    return {
      message: "",
      warning: false,
      array: ["즐겜", "미드"]
    };
  },
  methods: {
    submit: function() {
      this.warning = false;
      var counter = this.array.length;

      for (var i = 0; i < counter; i++) {
        if (this.array[i] == this.message) {
          this.warning = true;
          break;
        }
      }
      if (this.warning == false) {
        this.array.push(this.message);
        this.message = "";
      }
    },
    remove: function(x) {
      this.array.splice(x, 1);
    }
  }
  


}
</script>