<template>
  <div>
    <h2 class="text-center">SSAFY.GG - QnA</h2>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '40%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '15%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제 목</th>
          <th>질문자</th>
          <th>일 시</th>
        </tr>
        <tr v-for="(q, index) in items" :key="index + '_items'">
          <td>
            <router-link :to="'detail?no=' + q.qnaNo">{{
              q.qnaNo
            }}</router-link>
          </td>
          <td>
            <router-link :to="'detail?no=' + q.qnaNo">{{
              q.qnaTitle
            }}</router-link>
          </td>
          <td>{{ q.qnaUserid }}</td>
          <td>{{ getFormatDate(q.qnaDatetime) }}</td>
        </tr>
      </table>
    </div>
    <div v-else class="text-center">
      게시글이 없습니다.
    </div>
    <div>
      <router-link :to="'/save'"
        ><button class="btn btn-secondary">등록</button></router-link
      >
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

export default {
  name: "List",
  data: function() {
    return {
      query: "",
      items: []
    };
  },
  created() {
    axios.get("http://localhost:9999/happyhouse/api/qna").then(({ data }) => {
      console.log(data);
      this.items = data;
    });
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    }
  }
};
</script>
