<template>
  <div id="safearea" style="border: 1px solid;">

    <div><img id="GG" src="@/img/GG.png"/></div>

    <div>
    <button @click=click()>클릭</button>
    </div>

    <div id="logins">
      <NaverLogin
        class="text-center"
        client-id="o1YdNUHe3Pw0mLoqvsej"
        callback-url="https://i3a401.p.ssafy.io"
        v-bind:is-popup="false"
        v-bind:button-type="3"
        v-bind:button-height="48"
        button-color="green"
        :callbackFunction="callbackFunction"
      />
      <KakaoLogin
        api-key="33a2739cf555e249f8fdf2f60f535d32"
        image="kakao_account_login_btn_medium_narrow"
        :on-success="onSuccess"
        :on-failure="onFailure"
      />
    </div>  

    <div><img id="img" src="@/img/img1.png" style="text-center; position: relative; top:95px;" /></div>

 
  </div>
</template>


<script>
//style="margin-top: 87px; padding:0px"
import axios from "axios";
import NaverLogin from "vue-naver-login";
import KakaoLogin from "vue-kakao-login";

let onFailure = data => {
  console.log(data);
  console.log("failure");
};

let callbackFunction = status => {
  if (status) {
    /* (5) 필수적으로 받아야하는 프로필 정보가 있다면 callback처리 시점에 체크 */
    var email = NaverLogin.user.getEmail();
    if (email == undefined || email == null) {
      alert("이메일은 필수정보입니다. 정보제공을 동의해주세요.");
      /* (5-1) 사용자 정보 재동의를 위하여 다시 네아로 동의페이지로 이동함 */
      NaverLogin.reprompt();
      console.log(email);
      return;
    }

    window.location.replace(
      "http://" +
        window.location.hostname +
        (location.port == "" || location.port == undefined
          ? ""
          : ":" + location.port) +
        "/sample/main.html"
    );
  } else {
    console.log("callback 처리에 실패하였습니다.");
  }
};

export default {
  name: "App",
  data() {
    return {};
  },
  components: {
    NaverLogin,
    KakaoLogin
  },
  methods: {
    click(){
        var key = 'AAAA9uf5aY4:APA91bE-yYBS6gkcTWHH2JNvIjtBABg2KvnQdSeUi-5g-xbyhCz39VOPr8pgA_BB_WVd8Fm9SJ7kgsUFshji9Ujpmb5pQQGnysFFjVBPgA1qQq7vk20RGLzmsJeETiDdSpJQpzI_Cfhb';
        var to = localStorage.getItem('token');
        var notification = {
          'title': '알림을 받으셨습니다',
          'body': '누군가의 신청(나 자신)',
          'icon': 'firebase-logo.png',
          'click_action': 'https://i3a401.p.ssafy.io'
        };

        fetch('https://fcm.googleapis.com/fcm/send', {
          'method': 'POST',
          'headers': {
            'Authorization': 'key=' + key,
            'Content-Type': 'application/json'
          },
          'body': JSON.stringify({
            'notification': notification,
            'to': to
          })
        }).then(function(response) {
          console.log(response);
        }).catch(function(error) {
          console.error(error);
        })
    },
    onSuccess(data){
      console.log(data)
      console.log("success")
      axios.get('https://i3a401.p.ssafy.io:443/api/user/kakao', {
        params :{
          code : data.access_token
        }
        }).then(({ data }) => {
          console.log("data : "+data);
          this.$session.set("user_auth", data);
          console.log(this.$session.get("user_auth"));
          alert("환영합니다. " + data + "님");
          this.$router.push("/riot");
        });
    },
    onFailure,
    callbackFunction
  },
  created(){
    let uri = window.location.href.split('#');
    if (uri.length == 2)
    {
      let vars = uri[1].split('&');
      let vars2 = vars[0].split('=');
      axios.get('https://i3a401.p.ssafy.io:443/api/user/naver', {
        params :{
          code : vars2[1]
        }
      }).then(({ data }) => {
        console.log(data);
         this.$session.set("user_auth", data.response.email);
          console.log(this.$session.get("user_auth"));
          alert("환영합니다. " + data.response.id + "님");
          this.$router.push("/riot");
        });
    }
  }
  
}
</script>
<style scoped>
#safearea {
  min-width: 370px;  
  width: 100%;
  height: 700px;
  max-width: 550px;
  max-height: 700px;
  /* border: 1 solid black; */
  padding: 0;
}
#GG {
  width:230px; 
  height:120px; 
  margin-top: 90px;
}
#logins {
  margin-top: 90px;
}
</style>
