<template>

    


    <div class="text-align">
      <table>
        <tr>
          <th>제목</th>
          <th>{{ item.qnaTitle }}</th>
        </tr>
        <tr>
          <th>본문</th>
          <th>{{ item.qnaContent }}</th>
        </tr>
        <tr>
          <th>작성자</th>
          <th>{{ item.qnaUserid }}</th>
          <th>작성시간</th>
          <th>{{ getFormatDate(item.qnaDatetime) }}</th>
        </tr>
      </table>

      <div class="text-center">
        <router-link :to="'/list'"
          ><button class="btn btn-primary">목록</button></router-link
        >
        <router-link :to="'/update?no=' + item.qnaNo"
          ><button class="btn btn-secondary">수정</button></router-link
        >
        <router-link :to="'/delete?no=' + item.qnaNo"
          ><button class="btn btn-warning">삭제</button></router-link
        >
      </div>
    </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "Detail",
  data: function() {
    return {
      item: ""
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    alert(`${params.get("no")}`);
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get("no")}`)
      .then(({ data }) => {
        console.log(data);
        this.item = data;
      });
  },
  
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    }
  }
};
</script>
