import Vue from "vue";
import VueRouter from "vue-router";

import QnA_List from "@/layouts/QnA_List.vue";
import QnA_Save from "@/layouts/QnA_Save.vue";
import QnA_Detail from "@/layouts/QnA_Detail.vue";
import QnA_Delete from "@/layouts/QnA_Delete.vue";
import QnA_Update from "@/layouts/QnA_Update.vue";

import Login from "@/layouts/Login.vue";
import Home from "@/layouts/Home.vue";
import Riot from "@/layouts/Riot.vue";
import RiotConfirm from "@/layouts/RiotConfirm.vue";
import UserDetail from "@/layouts/UserDetail.vue";
import CardRegist from "@/layouts/CardRegist.vue";
import Hashadd from "@/layouts/Hashadd.vue";
import Alarm from "@/layouts/Alarm.vue"
import Matching_Complete from "@/layouts/Matching_Complete";
import Applicant from "@/layouts/Applicant.vue";

Vue.use(VueRouter);

export default new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "main",
      component: Login
    },
    {
      path: "/list",
      name: "list",
      component: QnA_List
    },
    {
      path: "/save",
      name: "save",
      component: QnA_Save
    },
    {
      path: "/detail",
      name: "detail",
      component: QnA_Detail
    },
    {
      path: "/delete",
      name: "delete",
      component: QnA_Delete
    },
    {
      path: "/update",
      name: "update",
      component: QnA_Update
    },
    {
      path: "/Home",
      name: "home",
      component: Home
    },
    {
      path: "/riot",
      name: "riot",
      component: Riot
    },
    {
      path: "/riotconfirm",
      name: "riotconfirm",
      component: RiotConfirm
    },
    {
      path: "/userdetail",
      name: "userdetail",
      component: UserDetail
    },
    {
      path: "/cardregist",
      name: "cardregist",
      component: CardRegist
    },
    {
      path: "/hashadd",
      name: "hashadd",
      component: Hashadd
    },
    {
      path: "/Alarm",
      name: "Alarm",
      component: Alarm
    },
    {
      path:"/Matching_Complete",
      name:"Matching_Complete",
      component:Matching_Complete
    },
    {
      path: "/Applicant",
      name: "Applicant",
      component: Applicant
    }

  ]
});
