<template>
  <div>
    <div>
      <div class="form-group">
        <label for="title">제목</label>
        <input
          type="text"
          class="form-control"
          id="qnaTitle"
          ref="qnaTitle"
          placeholder="제목을 입력하세요"
          v-model="qnaTitle"
        />
      </div>
      <div class="form-group">
        <label for="content">내용</label>
        <textarea
          class="form-control"
          id="qnaContent"
          ref="qnaContent"
          placeholder="내용을 입력하세요"
          v-model="qnaContent"
        ></textarea>
      </div>
      <div class="form-group">
        <label for="qnaUserid">이름</label>
        <textarea
          class="form-control"
          id="qnaUserid"
          ref="qnaUserid"
          placeholder="내용을 입력하세요"
          v-model="qnaUserid"
        ></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-primary" @click="handler">질문등록</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Save",
  data() {
    return {
      qnaTitle: "",
      qnaContent: "",
      qnaUserid: ""
    };
  },
  methods: {
    handler() {
      let err = true;
      let msg = "";
      !this.qnaTitle &&
        ((msg = "제목 입력해주세요"),
        (err = false),
        this.$refs.qnaTitle.focus());
      err &&
        !this.qnaContent &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.qnaContent.focus());
      if (!err) alert(msg);
      else this.createHandler();
    },
    createHandler() {
      axios
        .post("http://localhost:9999/happyhouse/api/qna", {
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaUserid: this.qnaUserid
        })
        .then(() => {
          alert("저장완료");
          this.$router.push("/list");
        });
    }
  }
};
</script>
