<template>
  <div>
    <div>
      <div class="form-group">
        <label for="title">제목</label>
        <input
          type="text"
          class="form-control"
          id="qnaTitle"
          ref="qnaTitle"
          placeholder="제목을 입력하세요"
          v-model="qnaTitle"
        />
      </div>
      <div class="form-group">
        <label for="content">내용</label>
        <textarea
          class="form-control"
          id="qnaContent"
          ref="qnaContent"
          placeholder="내용을 입력하세요"
          v-model="qnaContent"
        ></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-primary" @click="handler">질문등록</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Update",
  data() {
    return {
      qnaTitle: "",
      qnaContent: "",
      qnaUserid: "",
      qnaNo: ""
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get("no")}`)
      .then(({ data }) => {
        console.log(data);
        this.qnaTitle = data.qnaTitle;
        this.qnaContent = data.qnaContent;
        this.qnaUserid = data.qnaUserid;
        this.qnaNo = data.qnaNo;
      });
    console.log("완료");
  },
  methods: {
    handler() {
      alert(this.qnaTitle);
      this.createHandler();
    },
    createHandler() {
      axios
        .put("http://localhost:9999/happyhouse/api/qna/" + this.qnaNo, {
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaUserid: this.qnaUserid,
          qnaNo: this.qnaNo
        })
        .then(() => {
          alert("update success");
          this.$router.push("/list");
        });
    }
  }
};
</script>
